.recipe{
    position: relative;
    cursor: pointer;
    margin-top: 50px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif ;
    display: flex;
    background-color: rgb(143, 95, 145);
    padding: 20px;
    border-radius: 8px;
    font-size: large;
    box-shadow: 5px 10px #888888;
    width: 700px; 
    margin-bottom: 20px;
}

.recipe__head{
    display: flex;
    flex-direction: column;
    padding: 10px;
    justify-content: space-evenly;

}
img{
    height: 200px;
    object-fit: contain;
    border-radius: 4px;
    margin-right: 10px;
}

.instructions{
    text-align: center;
}

@media screen and (max-width:480px) {
    .recipe {
        text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
        width: 80%;
        margin-bottom: 20px;
        padding: 20px;
    }
  
  }